*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #3e5959;
}

code {
  font-family: 'Lato', sans-serif;
}

ul li {
  list-style: none;
  padding: 0;
  margin: 0;
}

li a {
  text-decoration: none;
}

.container {
    width: 90%;
    margin: auto;
}

.order-table {
  border: 1px solid black;
}

.order {
  display: flex;
  align-items: center;
}

.customer-name,
.customer-order {
  border: 1px solid black;
}


.divTable{
	display: table;
	width: 100%;
}
.divTableRow {
	display: table-row;
}
.divTableHeading {
	background-color: #EEE;
	display: table-header-group;
}
.divTableCell, .divTableHead {
	border: 1px solid #999999;
	display: table-cell;
  padding: 3px 10px;
}
.divTableHeading {
	background-color: #EEE;
	display: table-header-group;
	font-weight: bold;
}
.divTableFoot {
	background-color: #EEE;
	display: table-footer-group;
	font-weight: bold;
}
.divTableBody {
	display: table-row-group;
}

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}

.pickup-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.date-where-time {
  padding: 1rem;
  display: flex;
}

.spacer {
  height: 20px;
}


.box {
  padding: 1rem;
}

.address {
  margin-right: 50px;
}

input {
  margin: 1rem;
  padding: 1rem;
  font-size: 1rem;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 2px solid #eee;
}

.input-field {
  margin-top: 1rem;
  margin-left: 0;
  margin-right: 1rem;
  padding: 1rem;
  font-size: 1rem;
}


.submit-btn {
  width: 200px;
  background: #93BF00;
  border-radius: 30px;
  display: block;
  margin-top: 40px;
}


/********************************
Home Page
*********************************/

.light {
  color: #deecf1;
  font-size: 2.3rem;
}

h2 {
  color: #93BF00;
  font-size: 1.3rem;
}

.main-container {
  width: 80%;
  margin: auto;
  /* position: absolute; */
}

.hero-bkg{
  width: 100%;
  height: auto;
  background: url(/static/media/recycle-hero.b9acc812.jpg) center no-repeat;
  background-size: cover;
  /* z-index: -1;
  position: absolute;
  left: 0px;
  top: 0px; */
}



.main{
  padding-top: 100px;
  padding-bottom: 100px;
}

.intro {
  margin-right: auto;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 250px;
}

.title {
  font-size: 2.3rem;
}

.our-story-section {
  background: #F8FCFD;
  padding: 60px 0;
}

.our-story-details {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
  line-height: 1.8;
}

.our-story-details > a{
  color: #93BF00;
  margin-left: auto;
  margin-top: 50px;
}


.info {
  width: 90%;
  line-height: 1.5;
}

img {
  width: 50%;
  max-width: 100%;
}

.hero-img {
  display: none;
}

.footer {
  width: 100%;
  margin: auto;
  text-align: center;
  background: #f8fcfd;
  padding: 2rem 3rem;
}

/********************************
Categories
*********************************/

.categories-section {
  margin: auto;
}

.icon-img{
  max-width: 60px;
  display: center;
  margin: 30px;
  align-content: center;
  list-style-type: none;
}

.cat-container {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  justify-content: center;
}

.icon-flex{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 1.5rem;
}

#center-h1{
  text-align: center;
  padding: 1.5rem 0;
}

.bottom-text{
  line-height: 1.8;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto; 
  text-align: center;
  padding: 2rem;
  width: 90%;
}

.btn-1{
  margin-top: 3rem;
}

/**********************************
      Ticket Modal
***********************************/
.ticket-modal{
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  left: 50%;
  top:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-color: white;
  border:solid;
  width:60%;
  height:74%
}
.close-modal-btn{
  position : relative;
  width: 50px;
  height: 50px;
}
.form-top-cont{
  width: 100%;
  height:10%;
  display: flex;
  justify-content: flex-end;
}
.form-field > *{
  height: 30px
}
.table-container{
  position: relative;
  display: flex;
  flex-wrap: wrap;
}
.company-nav{
  display: flex;
  flex-flow: column;
  width: 25%;
  background-color: pink;
  position: relative;
  justify-content: space-around
}
.company-nav > div{
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  margin: 0;
  border: none;
  background-color: #3e5959;
  color: #fff;
}
.company-nav > div.active{
  background-color: #93bf00;
  border:none;
  color: #3e5859;
}
.company-dash-cont{
  display:flex;
  background-color:white;
  height: 90vh;
  margin-top: 100px;
  /* border: solid; */
}
.comp-ticket-cont{
  display:flex;
  width:77%

}
.user-ticket-cont{
  display:flex;
  width:77%;
  border:solid

}
.user-order{
  display:flex;
  width: 100%;
  border:solid;
  flex-wrap: wrap
}
.table-container{
  display: flex;
  flex-wrap:wrap;
  overflow-y: scroll;
  width:100%;
}
.ticket-table{
  display: flex;
  border: solid;
